"use client";

import React from "react";
import { useState, useEffect } from "react";
// Pour un environnement Next.js, vous utiliseriez 'next/image'
// Dans cet environnement isolé, nous utilisons les icônes lucide-react qui sont plus fiables.
import { Users, Gift, Settings, Info, Clock, Send, LogOut } from 'lucide-react'; 

// Composant réutilisable pour les cartes simples
const StatCard = ({ icon: Icon, title, value }) => (
    <div className="bg-gray-800 w-full h-80 flex flex-col justify-center items-center rounded-2xl border border-gray-700 shadow-2xl p-4">
        <Icon className="w-16 h-16 text-blue-400"/>
        <p className="text-white text-xl font-bold mt-5 text-center">{title}</p>
        {/* Style inline pour garantir la taille sur tous les navigateurs */}
        <p className="text-white font-extrabold mt-5" style={{ fontSize: '3rem' }}>{value}</p>
    </div>
);

export default function Accueil() {
    // États
    const [myclients, setMyclients] = useState(0);
    const [psn, setPsn] = useState(0);
    const [parrainage, setParrainage] = useState("Chargement...");
    const [timeLeft, setTimeLeft] = useState(0);
    
    // NOUVEAU : État pour gérer l'hydratation
    const [isMounted, setIsMounted] = useState(false);

    // 1. Montage Client et Hydratation
    useEffect(() => {
        // Le composant est monté côté client, on peut démarrer la logique dynamique
        setIsMounted(true);
    }, []);

    // 2. Vérification de l'authentification et Fetch des données
    useEffect(() => {
        if (!isMounted) return; // Ne pas exécuter si non monté

        const token = localStorage.getItem("token");
        
        if (!token) {
           // window.location.href = "/login";
           console.log("Token manquant, redirection vers /login...");
           return;
        }

        const fetchProfile = async () => {
            try {
                const res = await fetch("http://localhost:5000/getparain", {
                    method: "GET",
                    headers: {
                        "Authorization": `Bearer ${token}`,
                    },
                });

                const data = await res.json();
                if (res.ok) {
                    setMyclients(data.invited_count);
                    setParrainage(data.referral_code);
                    setPsn(data.psn_count);
                } else {
                    console.error("Erreur de l'API:", data.error);
                }
            } catch (error) {
                console.error("Erreur de connexion:", error);
            }
        };

        fetchProfile();
    }, [isMounted]); // Dépendance à isMounted

    // 3. Logique du Timer
    useEffect(() => {
        if (!isMounted) return; // Ne pas exécuter si non monté

        const targetDate = new Date("2025-12-15T00:00:00").getTime();

        const interval = setInterval(() => {
            const now = new Date().getTime();
            const difference = targetDate - now;

            if (difference <= 0) {
                clearInterval(interval);
                setTimeLeft(0);
            } else {
                setTimeLeft(difference);
            }
        }, 1000);

        return () => clearInterval(interval);
    }, [isMounted]); // Dépendance à isMounted


    const days = Math.floor(timeLeft / (1000 * 60 * 60 * 24));
    const hours = Math.floor((timeLeft / (1000 * 60 * 60)) % 24);
    const minutes = Math.floor((timeLeft / (1000 * 60)) % 60);
    const seconds = Math.floor((timeLeft / 1000) % 60);
    
    // Fonction de déconnexion
    const logout = () => {
        window.localStorage.clear();
        // window.location.href = "/login";
        console.log("Déconnexion effectuée et redirection vers /login...");
    }

    // Affiche un état de chargement tant que le client n'est pas monté
    if (!isMounted) {
        return (
            <div className="bg-white w-full min-h-screen flex justify-center items-center">
                <p className="text-gray-500 text-lg">Chargement du tableau de bord...</p>
            </div>
        );
    }

    // Le rendu principal ne se fait qu'après l'hydratation
    return (
        <div className="bg-white w-full min-h-screen flex justify-start items-center flex-col pt-10 pb-16 font-sans">
            
            {/* Conteneur des cartes (Responsive Grid) */}
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6 w-full max-w-6xl px-4">

                {/* 1. Carte Personnes Invitées */}
                <StatCard 
                    icon={Users}
                    title="Personnes invités :"
                    value={myclients}
                />

                {/* 2. Carte Cartes PSN */}
                <StatCard 
                    icon={Gift}
                    title="Cartes PSN en € :"
                    value={psn}
                />

                {/* 3. Carte Mes informations */}
                <div className="bg-gray-800 w-full h-80 flex flex-col justify-start items-center rounded-2xl border border-gray-700 shadow-2xl p-6">
                    <Settings className="w-16 h-16 text-blue-400 mt-4"/>
                    <p className="text-white text-xl font-bold mt-5">Mes informations :</p>
                    <p className="text-white text-md font-bold mt-5">Mon code de parrainage :</p>
                    <div className="mt-3 bg-stone-900 rounded-lg w-40 flex justify-center h-8 items-center px-4">
                        <p className="text-white font-bold tracking-wider">{parrainage}</p>
                    </div>
                </div>
            </div>

            {/* Deuxième rangée : Informations, Timer, Contact */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6 w-full max-w-6xl px-4 mt-6">
                <div className="col-span-1 md:col-span-3 bg-gray-800 w-full flex flex-col md:flex-row justify-around items-stretch rounded-2xl border border-gray-700 shadow-2xl p-6 gap-6">
                    
                    {/* Informations */}
                    <div className="flex flex-col items-center p-3 flex-1 min-w-0">
                        <Info className="w-12 h-12 text-blue-400"/>
                        <p className="text-white text-xl font-bold mt-3 text-center">Informations</p>
                        <p className="text-white text-sm mt-3 text-center opacity-80 max-w-xs">
                            - Chaque membre invité doit rejoindre le Telegram pour que son inscription soit comptée.
                        </p>
                    </div>

                    {/* Timer */}
                    <div className="flex flex-col items-center p-3 flex-1 min-w-0 border-y md:border-y-0 md:border-x border-gray-700">
                        <Clock className="w-12 h-12 text-blue-400"/>
                        <p className="text-white text-xl font-bold mt-3">Timer</p>
                        <p className="text-white text-2xl font-bold mt-5 text-center">
                            {days}j {hours}h {minutes}m {seconds}s
                        </p>
                    </div>

                    {/* Contact */}
                    <div className="flex flex-col items-center p-3 flex-1 min-w-0">
                        <Send className="w-12 h-12 text-blue-400"/>
                        <p className="text-white text-xl font-bold mt-3">Contact</p>

                        <a href="URL_TELEGRAM_CONTACT" target="_blank" rel="noopener noreferrer" className="mt-6">
                            <button className="bg-gradient-to-r from-blue-400 to-blue-600 text-white font-semibold px-6 py-3 rounded-2xl shadow-lg hover:from-blue-500 hover:to-blue-700 transition-all duration-300 cursor-pointer w-40 h-12 flex items-center justify-center gap-2">
                                <Send className="w-5 h-5" />
                                <p className="text-lg">Telegram</p>
                            </button>
                        </a>
                    </div>
                </div>
            </div>


            {/* Bouton Déconnexion */}
            <div className="mt-12 w-full max-w-6xl px-4 flex justify-center">
                <button 
                    onClick={logout}
                    className="bg-gradient-to-r from-red-500 to-red-800 rounded-2xl w-full sm:w-60 h-12 
                    cursor-pointer hover:from-red-600 hover:to-red-900 
                    transform hover:scale-[1.02] transition-all duration-300 
                    flex items-center justify-center gap-2 font-bold text-white text-xl shadow-xl"
                >
                    <LogOut className="w-6 h-6 text-white"/>
                    Déconnexion
                </button>
            </div>
        </div>
    )
}